<template>
    <div class="recommend">
        <Title>推荐歌单</Title>
        <div class="musicBox">
            <ul class="recommendList">
                <router-link v-for="rem in recommendMusicList" :key="rem.id" to="/" tag="li">
                    <div>
                        <img :src="rem.picUrl"/>
                        <span>{{rem.playCount|formatNum}}</span>
                    </div>
                    <p>{{rem.name}}</p>
                </router-link>
            </ul>
        </div>
        <Title>最新音乐</Title>
        <Musicitem :newMusicList="newMusicList"></Musicitem>
    </div>
</template>

<script>
import Title from '../components/Title'
import Musicitem from '../components/Musicitem'

export default {
    name:"Recommend",
    components:{
        Title,
        Musicitem
    },
    data(){
        return{
            recommendMusicList:[],
            newMusicList:[]
        }
    },
    beforeRouteEnter(to,from,next){
        next(vm=>{
            vm.axios.get('/personalized?limit=6')
            .then(data=>{
                vm.recommendMusicList = data.data.result;
            });
            vm.axios.get('/personalized/newsong')
            .then(data=>{
                vm.newMusicList = data.data.result;
            });
        })
    },
    filters:{
        formatNum(value){
            return (value / 10000).toFixed(1)+"万"; 
        }
    }
}
</script>
<style lang="less" scoped>
    .musicBox{
        .recommendList{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            li{
                width: 33%;
                margin-bottom: 13px;
                div{
                    position: relative;
                    span{
                        position: absolute;
                        right: 5px;
                        top: 2px;
                        color: white;
                        font-size: 12px;
                        text-shadow: 1px 0 0 rgba(0,0,0,.15);
                        &::after{
                            position: absolute;
